@import "@theme/vars.scss";

.schedule-picker {
	display: flex;
	flex-direction: column;
	width: 100%;

	&__days {
		display: flex;
		margin-bottom: 20px;

		@media only screen and (max-width: $bp-lg) {
			margin-bottom: 16px;
		}

		@media only screen and (max-width: $bp-md) {
			margin-bottom: 12px;
		}
	}

	&__day {
		cursor: pointer;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		height: 52px;
		flex-grow: 1;
		padding: 0 5px;
		margin-right: 8px;
		background-color: $light;
		color: $dark-light;
		font-weight: 600;
		font-size: 20px;
		line-height: 32px;
		border: 1px solid #E5E7EB;
        transition: background-color 0.35s ease, color 0.35s ease, fill 0.35s ease, border-color 0.35s ease;

		@media only screen and (max-width: $bp-lg) {
			height: 40px;
			font-size: 16px;
		}

		@media only screen and (max-width: $bp-md) {
			height: 32px;
			padding: 0 2px;
			font-size: 14px;
		}

		&:last-child{
			margin-right: 0;
		}
		
        &:hover{
            background-color: $light-light;
        }

        &:active{
            background-color: #E5E7EB;
            border-color: #D1D5DB;
        }

		&.is-active{
			background-color: $blue;
			border-color: $blue;
			color: $light;
			
			&:hover{
				background-color: $blue-dark;
			}
		
			&:active{
				background-color: $blue-light;
			}
		
		}

		input{
			display: none;
		}
	}

	&__input {
		max-width: 50%;

		@media only screen and (max-width: $bp-md) {
			max-width: none;
		}
	}

	&__day-wrapper {
	}
}