@import "@theme/vars.scss";

.map{
    position: relative;
    width: 100%;
    height: 400px;

    &__content{
        width: 100%;
        height: 100%;
    }

    &__dot{
        display: flex;
        align-items: center;
        justify-content: center;
        fill: #fff;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        background-color: $blue;
        transform: translate(-50%, -50%);

        .icon{
            position: relative;
            z-index: 2;
            width: 20px;
            height: 20px;
            margin-right: 1px;
        }

        &--artist{
            box-sizing: border-box;
            background: #F3F4F6;
            border: 1px solid $light-light;
            background-color: $light;
            fill: $blue;
            box-shadow: 0px 20px 25px -5px rgba(16, 24, 40, 0.1), 0px 8px 10px -6px rgba(16, 24, 40, 0.1);
        }

        &--artist-md{
            background-color: $blue-white;
            fill: $light;
            box-shadow: 0px 1px 3px rgba(16, 24, 40, 0.1), 0px 1px 2px -2px rgba(16, 24, 40, 0.1);
        }

        &--artist-ld{
            background-color: $blue;
            fill: $light;
            box-shadow: 0px 20px 25px -5px rgba(16, 24, 40, 0.1), 0px 8px 10px -6px rgba(16, 24, 40, 0.1);
        }

        span{
            position: relative;
            z-index: 2;
            box-sizing: border-box;
            width: 26px;
            height: 26px;
            flex-shrink: 0;
            border-radius: 50%;
            border: 5px solid #fff;
        }

        &.is-active{
            background-color: #2F54EB;
        }
    }

    &__info{
        box-sizing: border-box;
        padding: 16px;
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 12px;
        right: 12px;
        background: rgba(0, 0, 0, 0.7);
        box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.12);
        backdrop-filter: blur(2px);
        max-width: 200px;
    }

    &__info-item{
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 14px;
        color: $light;
        margin-bottom: 16px;

        &:last-child{
            margin-bottom: 0;
        }
        span{
            width: 12px;
            height: 12px;
            flex-shrink: 0;
            border-radius: 50%;
            background-color: $light-light;
            margin: 1px 8px auto 0;
        }

        strong{
            font-weight: 600;
        }
    }
}