@import "@theme/vars.scss";

.calendar-table {   
    width: 100%;

    &--edit &__day-add{
        display: flex;
    }

    &__header {
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px 0;
        margin-bottom: 24px;
        color: $dark-light;

        @media only screen and (max-width: $bp-lg) {
            padding: 0;
        }

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: start;
        }
    }

    &__title {
    }

    &__info {
        display: flex;
        align-items: center;
        margin-left: 16px;

        &:first-child{
            margin-left: 0;
            margin-top: 0;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            margin-top: 16px;
        }
    }

    &__month {
        span{
            animation: fadeIn 0.5s ease;
        }
    }

    &__year {
        font-weight: 400;
        margin-left: 16px;

        span{
            animation: fadeIn 0.5s ease;
        }
    }

    &__nav{
        margin: 0 -5px;
        padding-left: 11px;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }

        @media only screen and (max-width: $bp-md) {
            margin-left: auto;
            padding-left: 16px;
        }

        .slider__button {
            width: 24px;
            height: 24px;
            flex-shrink: 0;
            margin: 0 5px;
            background-color: transparent;
            color: #374151;
            fill: #374151;
            border-radius: 0;
    
            &:hover{
                background-color: rgba(221, 205, 255, 0.1);
            }
    
            &:active{
                background-color: rgba(215, 199, 255, 0.5);
            }
        }
    }

    &__slider { 
        background-color: $light;

        .slider {

            &__inner{
                @media only screen and (max-width: $bp-lg) {
                    box-sizing: border-box;
                    margin-right: -16px;
                    margin-left: -16px;
                    padding-right: 16px;
                    padding-left: 16px;
                    width: calc(100% + 32px);
                }
            }
    
            &__slide {
                width: 100%;

                @media only screen and (max-width: $bp-lg) {
                    min-width: 100%;
                    width: auto !important;
                }
            }
        }
    }

    &__main {
        max-width: 100%;
        overflow: auto hidden;
    }

    &__thead {
        background-color: $light;
    }

    &__thead,
    &__tbody {
        box-sizing: border-box;
        display: grid;
        padding: 1px;
        gap: 1px;
        grid-template-columns: repeat(7, calc((100% - 6px) / 7));
        min-width: calc(110px * 7 + 8px );
    }

    &__thead-col {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 38px;
        padding: 0 5px;
        color: #4B5563;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;

        @media only screen and (max-width: $bp-lg) {
            font-size: 12px;
            height: 28px;
        }
    }

    &__tbody {
        background-color: $light-light;
    }

    &__day {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 118px;
        min-width: 110px;
        padding: 2px;
        background-color: #fff;
        transition: background-color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            height: 105px;
            max-width: 140px;
        }

        @media only screen and (max-width: $bp-md) {
            min-width: 100px;
            max-width: 130px;
        }

        &:hover{
            background-color: #F3F4F8;
        }

        &[disabled] {
            pointer-events: none;
        }
    }

    &__day--today &__day-number span{
        width: 16px;
        height: 16px;
        border-radius: 50%;
        flex-shrink: 0;   
        background-color: $blue;
        color: #fff;
        font-size: 10px;
    }
    
    &__day-number {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        flex-shrink: 0;
        margin-bottom: 2px;
        font-size: 12px;
        line-height: 100%;
        color: #252525;
        transition: color 0.25s ease, opacity 0.25s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 10px;
            width: 20px;
            height: 20px;
        }

        span{
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__day-list {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-bottom: 4px;
        overflow-y: auto;

        &::-webkit-scrollbar {
            height: 4px;
            width: 4px;
        }
    }

    &__day-event {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        width: 100%;
        font-size: 12px;
        line-height: 100%;
        padding: 2px 4px;
        margin-bottom: 2px;
        transition: background-color 0.25s ease, opacity 0.25s ease;
        animation: fadeInTop 0.3s cubic-bezier(.66,.39,.21,.67) both;

        @media only screen and (max-width: $bp-lg) {
            font-size: 10px;
        }

        &:nth-child(3n - 2){
            background-color: #FFD494;
        }

        &:nth-child(3n - 1){
            background-color: #FEE2E2;
        }

        &:nth-child(3n){
            background-color: #F3F4F6;
        }

        &:last-child{
            margin-bottom: 0;
        }

        h4{
            font-weight: 400;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            color: $dark-light;
        }

        span{
            color: #374151;
            flex-shrink: 0;
            margin-left: auto;
            padding-left: 8px;

            @media only screen and (max-width: $bp-lg) {
                padding-left: 6px;
            }
        }
    }

    &__day-actions {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        width: 100%;
        flex-shrink: 0;
        margin-top: auto;
    }

    &__day:hover &__day-add{
        opacity: 1;
    }

    &__day-add {
        display: none;
        width: 20px;
        height: 20px;
        padding: 0;
        flex-shrink: 0;
        opacity: 0;
        margin: 0 8px 2px 2px;

        .button{
            &__icon{
                width: 12px;
                height: 12px;
            }
        }
    }

    &__day-link {
        display: none;
        font-size: 10px;
        line-height: 10px;
        margin-left: auto;

        &.is-visible{
            display: inline-flex;
            animation: fadeIn 0.35s ease;
        }
    }

    
    &__day[disabled] &__day-list{
        opacity: 0.3;
        -webkit-tap-highlight-color: transparent;
        scrollbar-color: $light-medium transparent;
    
        &::-webkit-scrollbar-track {
            background-color: transparent;
        }
    
        &::-webkit-scrollbar-thumb {
            background-color: $light-medium;
        }
    }

    &__day[disabled] &__day-event{
        background-color: #E5E7EB;
    }

    &__day[disabled] &__day-number{
        opacity: 0.5;
        color: #9CA3AF;
    }

    &__day[disabled] &__day-link{
        display: none;
    }
}

