@import "@theme/vars.scss";


.card-actions{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    z-index: 2;

    &__item{
        flex-shrink: 0;
        padding: 0;
        width: 24px;
        height: 24px;
        margin-left: 20px;

        &:first-child{
            margin-left: 0;
        }

        .icon{
            width: 100%;
            height: 100%;
        }

        &--delete{
            fill: #DC2626;

            &:hover{
                fill: #991B1B;
            }
        }
    }
}