@import "@theme/vars.scss";

.card-event {
    position: relative;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background-color: $light;
    text-decoration: none;
    color: $dark;
    padding: 24px;
    transition: background-color 0.35s ease, color 0.35s ease;
    animation: fadeIn 0.45s ease forwards;

    @media only screen and (max-width: $bp-lg) {
        padding: 8px 16px 16px;
    }

    &>*{
        &:last-child{
            margin-bottom: 0;
        }
    }

    &__actions{
        position: absolute;
        top: 24px;
        right: 24px;

        @media only screen and (max-width: $bp-lg) {
            top: 8px;
            right: 16px;
        }

        &>*{
            margin-left: 20px;

            @media only screen and (max-width: $bp-md) {
                margin-left: 0;
            }

            &:first-child{
                margin-left: 0;
            }
        }
    }

    &__actions ~ &__header{
        padding-right: 76px;
    }

    &__header{
        box-sizing: border-box;
        position: relative;
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-bottom: 16px;
        transition: color 0.35s ease;

        &:hover{
            color: $blue-light;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__subtitle{
        display: inline-flex;
        width: 100%;
        font-weight: 700;
        font-size: 16px;
        line-height: 28px;
        color: $orange;
        margin-bottom: 4px;

        &:last-child{
            margin-bottom: 0;
        }
        
        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 14px;
        }
    }

    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        width: 100%;
        flex-shrink: 0;
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.02em;
        color: $dark-light;
        
        @media only screen and (max-width: $bp-xl) {
            font-size: 22px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            -webkit-line-clamp: 2;
        }
    }

    &__list{
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__row{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 -6px;
        
        &:last-child{
            margin-bottom: -8px;

            @media only screen and (max-width: $bp-lg) {
                margin-bottom: -12px;
            }
        }
    }

    &__item{
        box-sizing: border-box;
        max-width: 100%;
        display: inline-flex;
        align-items: center;
        padding: 0 6px;
        margin-bottom: 8px;
        text-decoration: none;  
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        fill: $orange;
        color: $gray-blue;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 22px;
            margin-bottom: 12px;
        }

        .icon{
            width: 20px;
            height: 20px;
            margin-right: 12px;

            @media only screen and (max-width: $bp-md) {
                margin-right: 8px;
            }
        }

        .icon + span{

            @media only screen and (max-width: $bp-lg) {
                margin-left: 0;
            }
        }

        span{

            @media only screen and (max-width: $bp-md) {
                max-width: 100%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        &--gray{
            fill: #101010;
        }

        &--blue{
            color: $blue;
        }
    }

    &--demo{
        padding: 8px 8px 8px 0;
        background-color: transparent;
    }

    &--demo &__actions {
        top: 8px;
        right: 8px;
    }

    &--demo &__header{
        margin-bottom: 8px;
    }

    &--demo &__title{
        font-size: 14px;
        line-height: 22px;
    }

    &--demo &__subtitle{
        font-size: 14px;
        line-height: 14px;
    }

    &--demo &__list{

    }

    &--demo &__row{

    }

    &--demo &__item{
        font-size: 14px;
        line-height: 22px;

        .button{
            height: 24px;
            font-size: 14px;
            padding: 0 6px;
        }
    }
}