@import "@theme/vars.scss";
$tooltip-color:                     $blue-dark;
$tooltip-bg:                        #fff;
$tooltip-border-radius:             10;
$tooltip-opacity:                   1;

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/tooltip";
@import "~bootstrap/scss/transitions";

.tooltip{
    z-index: 30;
    &-inner{
        padding: 10px 16px;
        background-color: #fff;
        border-radius: 0;
        box-shadow: 0px 0px 44px #E1F4FF;
        width: 250px;
        font-size: 14px;
        color: #000;
        line-height: 120%;
        text-align: left;
    }

    &-arrow{
        display: none !important;
    }

    &-trigger{
        display: inline-flex;
        align-items: center;
        position: relative;
        fill: #6B7280;
        color: #6B7280;
        transition: color 0.35s ease, fill 0.35s ease;
    
        &__icon{
            cursor: pointer;
            width: 24px;
            height: 24px;
            flex-shrink: 0;
        }

        &:hover{
            color: $blue;
            fill: $blue;
        }
    }
}