@import "@theme/vars.scss";

.card-place {
    position: relative;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: wrap;
    background-color: $light;
    text-decoration: none;
    color: $dark;
    padding: 24px 24px 24px 328px;
    transition: background-color 0.35s ease, color 0.35s ease;
    animation: fadeIn 0.45s ease forwards;

    @media only screen and (max-width: $bp-xl) {
        padding-left: 240px;
    }

    @media only screen and (max-width: $bp-lg) {
        padding: 16px 16px 16px 196px;
    }

    @media only screen and (max-width: $bp-md) {
        padding: 0 0 16px;
        flex-direction: row;
        align-items: center;
    }

    &>*{
        &:last-child{
            margin-bottom: 0;
        }
    }


    &__actions{
        position: absolute;
        top: 16px;
        right: 16px;

        @media only screen and (max-width: $bp-md) {
            top: 26px;
        }

        &>*{
            margin-left: 20px;

            &:first-child{
                margin-left: 0;
            }
        }
    }

    &__actions ~ &__header{
        padding-right: 76px;
    }

    &__img-wrapper{
        display: flex;
        width: 304px;
        height: 100%;
        flex-shrink: 0;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;

        @media only screen and (max-width: $bp-xl) {
            width: 216px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 180px;
        }
        
        @media only screen and (max-width: $bp-md) {
            position: relative;
            width: 80px;
            height: 80px;
            margin-right: 16px;
            margin-bottom: 16px;
        }

        &:hover img{
            transform: scale(103%);
        }
    }
 
    &__img {
        display: flex;
        width: 100%;
        height: 100%;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            will-change: transform;
            transition: transform 0.75s ease;
        }
    }

    &__inner {
        box-sizing: border-box;
        position: relative;
        z-index: 3;
        height: 100%;
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        max-width: 100%;
        overflow: hidden;
    }

    &__content{
        box-sizing: border-box;
        width: 100%;

        @media only screen and (max-width: $bp-md) {
            padding-right: 16px;
            padding-left: 16px;      
        }
    }

    &__header{
        box-sizing: border-box;
        position: relative;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        width: 100%;
        text-decoration: none;
        color: $dark;
        margin-bottom: 16px;
        padding-right: 40px;
        transition: color 0.35s ease;
        
        @media only screen and (max-width: $bp-lg) {
            padding-right: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            width: calc(100% - 112px);
            align-self: flex-start;
        }

        &:hover{
            color: $blue-light;
        }
    }

    &__title {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 3;
        width: 100%;
        font-weight: 700;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.02em;
        flex-shrink: 0;
        
        @media only screen and (max-width: $bp-xl) {
            font-size: 22px;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
            line-height: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
            -webkit-line-clamp: 2;
        }
    }

    &__subtitle{
        display: inline-flex;
        width: 100%;
        font-weight: 400;
        font-size: 16px;
        line-height: 28px;
        color: #9CA3AF;
        margin-bottom: 4px;
        margin-top: 4px;
        
        &:first-child{
            margin-top: 0;
        }

        &:last-child{
            margin-bottom: 0;
        }
        
        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 14px;
        }
    }

    &__list{
        display: flex;
        flex-direction: column;
        margin-bottom: 8px;
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 0;
        }
    }

    &__row{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 -6px;
        
        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
            align-items: flex-start;
        }

        &:last-of-type{
            margin-bottom: -8px;
        }
    }

    &__item{
        box-sizing: border-box;
        max-width: 100%;
        display: inline-flex;
        align-items: center;
        padding: 0 6px;
        margin-bottom: 8px;
        text-decoration: none;  
        font-weight: 600;
        font-size: 16px;
        line-height: 28px;
        fill: $orange;
        color: $dark-light;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            font-size: 14px;
            line-height: 22px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 100%;
        }

        &:hover:is(a){
            color: $blue-light;
        }

        & > .icon{
            width: 16px;
            height: 16px;
            margin-right: 12px;

            @media only screen and (max-width: $bp-md) {
                margin-right: 8px;
            }
        }

        & > .icon + span{

            @media only screen and (max-width: $bp-lg) {
                margin-left: 0;
            }
        }

        & > span{

            @media only screen and (max-width: $bp-md) {
                margin-left: 24px;
                max-width: 100%;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }

        .button{
            border-radius: 50px;
            border: 0;

            @media only screen and (max-width: $bp-lg) {
                height: 24px;
                padding: 0 6px;
            }
        }
        
        &::after{
            content: "";
            display: inline-flex;
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: #C9C9C9;
            margin-left: 12px;

            @media only screen and (max-width: $bp-md) {
                content: none;
            }
        }

        &:last-of-type::after{
            content: none;
        }

        &--gray{
            fill: $dark-light;

            &>.icon{
                width: 20px;
                height: 20px;
            }
        }
    }

    &__text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        -webkit-line-clamp: 2;
        font-family: 'Literata';
        font-size: 16px;
        line-height: 24px;
        color: $gray-blue;
        margin-top: 8px;

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }

    &__event-block{
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        flex-wrap: wrap;
        margin: 16px 0 4px;

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__event-button{
        margin-top: 16px;
    }

    &__event{
        padding-right: 16px;
    }

    &__event-title{
        font-size: 16px;
        line-height: 24px;
        color: #000;
        margin-bottom: 8px;
    }

    &__event-name{
        font-weight: 700;
        font-size: 14px;
        line-height: 22px;
        color: $dark;
    }

    &__event-list{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: 0 -8px;
    }

    &__event-item{
        display: inline-flex;
        align-items: center;
        margin: 4px 8px 0;
        color: $gray-blue;
        fill: #101010;
        font-weight: 700;
        font-size: 14px;
        line-height: 22px;

        .icon{
            width: 20px;
            height: 20px;
            margin-right: 4px;
        }

        span{

        }
    }

    &__buttons{
        display: flex;
        align-items: center;
        margin: 12px -4px 0;

        &>*{
            margin: 8px 4px 0;
        }

        .button{
            @media only screen and (max-width: $bp-md) {
                height: 40px;
                flex-grow: 1;
            }
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 8px;
        }
    }    

    .like{
        top: 0;
        right: 0;
        stroke: $blue;

        @media only screen and (max-width: $bp-lg) {
            width: 16px;
            height: 16px;
        }

        &:active{
            stroke: $blue;
        }
    
        &.is-favorite{
            stroke: #EC7D42;
    
            &:hover{
                stroke: #BA531A;
            }
        
            &:active{
                stroke: #A23F02;
            }
        }
    }



    // md

    &--md {
        background-color: #fff;
        padding: 24px 16px 24px 264px;

        @media only screen and (max-width: $bp-xl) {
            padding-left: 232px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 16px 16px 16px 196px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 0 0 16px;
        }

    }

    &--md &__img-wrapper{
        width: 248px;

        @media only screen and (max-width: $bp-xl) {
            width: 216px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 180px;
        }

        @media only screen and (max-width: $bp-md) {
            width: 80px;
            height: 80px;
        }
    }

    &--md &__title{
        font-size: 18px;
        line-height: 24px;

    }

    &--md &__subtitle{
        font-size: 14px;
        line-height: 100%;
    }

    &--md &__item{
        font-size: 14px;
        line-height: 22px;

        .button{
            height: 32px;
            font-size: 14px;
            padding: 0 10px;

            @media only screen and (max-width: $bp-lg) {
                height: 24px;
                padding: 0 6px;
            }
        }
    }

    &--md &__text{
        font-size: 14px;
        line-height: 20px;
    }



    // sm

    &--sm .like {
        display: none;
    }

    &--sm{
        background-color: #fff;
        color: $gray-blue;
        min-height: 88px;
        padding: 8px 8px 8px 104px;

        @media only screen and (max-width: $bp-md) {
            padding-left: 96px;
            flex-direction: column;
        }

        &::before{
            content: none;
        }
    }
    
    &--sm &__img-wrapper{
        position: absolute;
        top: 0;
        left: 0;
        width: 88px;
        height: 100%;
        margin: 0;
    }


    &--sm &__header{
        width: 100%;
        padding-right: 0;
        margin-bottom: 8px;

        @media only screen and (max-width: $bp-md) {
            padding-left: 0;
            min-height: auto;
            margin-bottom: 4px;
        }
    }

    &--sm &__title{
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 4px;
        -webkit-line-clamp: 2;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 0;
        }
    }

    &--sm &__subtitle{
        font-size: 14px;
        line-height: 100%;

        @media only screen and (max-width: $bp-md) {
            display: none;
        }
    }

    &--sm &__content{
        @media only screen and (max-width: $bp-md) {
            padding-right: 0;
            padding-left: 0;
        }
    }

    &--sm &__list{
        margin-bottom: 0;
    }

    &--sm &__item{
        font-size: 14px;
        line-height: 22px;
        overflow: hidden;
        white-space: nowrap;

        .icon{
            width: 16px;
            height: 16px;
        }
    }

    &--sm &__date{
        margin-left: auto;
        padding-left: 4px;
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
    }

    &--sm &__text{
        font-size: 14px;
        line-height: 22px;
        max-height: 88px;
    }



    // rg
    
    &--rg .like{
        display: none;
    }
    
    &--rg {
        padding: 16px 16px 16px 216px;

        @media only screen and (max-width: $bp-md) {
            padding: 0 0 16px;
        }
    }

    &--rg &__img-wrapper{
        width: 200px;

        @media only screen and (max-width: $bp-md) {
            width: 80px;
            height: 80px;
        }
    }

    &--rg &__status{
        @media only screen and (min-width: $bp-md + 1) {
            order: 3;
            margin-top: 8px;
            margin-bottom: 0;
        }
    }

    &--rg &__header{
        margin-bottom: 8px;
    }

    &--rg &__title{
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 0;
    }

    &--rg &__subtitle{
        font-size: 14px;
        line-height: 22px;
    }
    
    &--rg &__list{
        margin-bottom: 8px;

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 0;
        }
    }

    &--rg &__item{
        font-size: 14px;
        line-height: 22px;
    }

    &--rg &__text{
        font-size: 14px;
        line-height: 22px;
    }
}
