@import '@theme/vars.scss';

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";
@import "~bootstrap/scss/modal";
@import "~bootstrap/scss/transitions";

.fade{
	transition: opacity 0.25s linear;
}

.modal {
    --bs-modal-zindex: 50;

	&.fade &-dialog{
        transform: none !important;
	}

	&.fade &-content{
		transform: translateY(15px);
        opacity: 0;
		transition: transform 0.25s ease-out, opacity 0.25s ease;

	}

	&.show &-content{
        opacity: 1;
		transform: none;
	}

	&-backdrop{
        --bs-backdrop-zindex: 45;
        background: rgba(0, 0, 0, 0.3);
        opacity: 0;
        backdrop-filter: blur(3px);

		&.show{
			opacity: 1;
		}
	}

	&--xl &-dialog{
        max-width: 1060px;
	}

	&--lg &-dialog{
        max-width: 700px;
	}
	
	&--md &-dialog{
        max-width: 614px;
	}
	

	&-dialog {
		border-radius: 0;
		border: none;
		max-width: 448px;
        min-height: 100%;
        margin: 0 auto;
	}

	&-content {
		box-sizing: border-box;
		display: flex;
		background-color: #fff;
        color: $dark;
        padding: 24px 24px 32px;
        border-radius: 0;
        border: 1px solid #E5E7EB;

        @media only screen and (max-width: $bp-lg) {
            padding: 24px 16px 32px;
        }
	}

	&-close {
		width: 16px;
		height: 16px;
		background-color: transparent;
		display: flex;
		align-items: center;
		justify-content: center;
		border: none;
		cursor: pointer;
        margin-left: auto;
        margin-bottom: 8px;

		.icon {
			width: 100%;
			height: 100%;
			transition: fill 0.35s ease;
			fill: $blue-dark;
		}

		&:hover {
			.icon{
				fill: $blue-light;
			}
		}
	}

	&__main{
        display: flex;
        flex-direction: column;
	}

        
	&__title{
        display: flex;
        margin-bottom: 16px;
        
        &:last-child{
            margin-bottom: 0;
        }

        span{
            color: $light-medium;
            margin-left: auto;
        }
    }

    &__text{
        margin-bottom: 16px;
    }

    &__repost-copy {
        width: 100%;
        margin-bottom: 16px;
        color: #6B7280;
        fill: #6B7280;
        justify-content: space-between;

        &:hover{
            fill: $dark;
            color: $dark;
        }

        .button__text{
            max-width: 100%;
            overflow: hidden;   
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }

        &.is-active{
            border-color: $blue;
            color: $blue;
            fill: $blue;
            transition-duration: 0.75s;
        }
    }

    &__repost-copy + &__text{
        padding-top: 16px;
        border-top: 1px solid #E5E7EB;
    }

    &__socials {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }

    &__socials-link {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 48px;
        height: 48px;
        flex-shrink: 0;
        margin-right: 8px;
        fill: $blue;
        transition: fill 0.35s ease;

        .icon{
            width: 100%;
            height: 100%;
        }

        &:hover{
            fill: $violet-dark;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__button-close {
        display: flex;
        margin-top: 32px;
        width: 100%;
    }

    &__schelude-item {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        font-size: 16px;
        line-height: 28px;
        margin-bottom: 12px;
        padding: 0 8px;
        color: $dark;
        transition: background-color 0.35s ease;

        &:hover{
            background-color: $light;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__organizers {
        margin-top: 32px;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -8px -16px;
    }

    &__organizers-item {
        box-sizing: border-box;
        width: 50%;
        display: block;
        padding: 0 8px;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-md) {
            width: 100%;
            margin-bottom: 8px;
        }
    }

    &__organizers-title {
        font-weight: 400;
        font-size: 16px;
        line-height: 100%;
        margin-bottom: 8px;
        color: #6B7280;
    }

    &__organizers-profile {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        padding: 8px;
        color: $dark;
        text-decoration: none;
        transition: background-color 0.35s ease;

        &:hover{
            background-color: #E5E7EB;
        }
    }

    &__organizers-img {
        display: flex;
        width: 32px;
        height: 32px;
        flex-shrink: 0;
        margin-right: 12px;
        border-radius: 50%;
        overflow: hidden;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__organizers-name {
        font-size: 16px;
        line-height: 100%;
        font-weight: 600;

    }

    &__form {

        &--grid{
            form{
                display: flex;

                @media only screen and (max-width: $bp-lg) {
                    flex-direction: column;
                    align-items: center;
                }
            }
        }
    }

    &__form-grid{
        display: flex;
        flex-wrap: wrap;
        margin: 0 -16px;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin: 0;
        }

        &>*{
            flex: 1 0 50%;
            padding: 0 16px;

            @media only screen and (max-width: $bp-lg) {
                width: 100%;
                flex: initial;
                padding: 0;
            }
        }
    }

    &__form-grid &__input{
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            .input{
                max-width: none;
            }
        }
        
        @media only screen and (max-width: $bp-md) {
            margin-bottom: 16px;
        }
    }

    &__input-img{
        width: 148px;
        height: 148px;
        flex-shrink: 0;
        margin-right: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 32px;
            margin-right: 0;
        }
    }

    &__input{
        box-sizing: border-box;
        margin-bottom: 16px;

        &--file{
            margin-top: 32px;
        }
    }

    &__select{
        box-sizing: border-box;
        flex-basis: 100%;
        margin-bottom: 24px;

        &:last-of-type{
            margin-bottom: 32px;
        }
    }

    &__textarea{
        box-sizing: border-box;
        margin-bottom: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 16px;
        }

        .input{
            &__field{
                height: 204px;

                @media only screen and (max-width: $bp-lg) {
                    height: 150px;
                }
            }
        }
    }

    &__textarea + .checkbox{
        margin-top: -22px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 0;
        }
    }

    &__input-label{
        font-size: 16px;
        line-height: 28px;
        color: #6B7280;
        margin-bottom: 4px;
    }

    &__form-buttons{
        display: flex;
        align-items: center;
        margin-top: 32px;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
        }
    }

    &__form-buttons &__button{
        height: 44px;
    }

    &__button{
        display: flex;
        width: 100%;
    }

    &__grid{
        max-height: 420px;
        overflow: hidden auto;
    }


    &__steps{
        display: flex;
        overflow: hidden;
        width: 100%;
    }

    &__steps > &__main{
        width: 100%;
        flex-shrink: 0;
    }

    &__step{
        background-color: #fff;
        transition: transform 0.75s cubic-bezier(0.75, 0.1, 0.15, 1);

        &.is-active{
            transform: translateX(-100%);
        }
    }
}

.modal-form {
    flex-grow: 1;
    margin-top: 16px;

    &:first-child{
        margin-top: 0;
    }

    form{
        height: 100%;
    }

    &__inner {
        display: flex;
        height: 100%;

        @media only screen and (max-width: $bp-md) {
            flex-direction: column;
        }
    }

    &__aside {
        display: flex;
        flex-direction: column;
        margin-right: 32px;
        flex-shrink: 0;

        &:last-child{
            margin-right: 0;
            margin-bottom: 0;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
            margin-bottom: 32px;
        }
    }

    &__main {
        display: flex;
        flex-direction: column;
        width: 100%;
        flex-grow: 1;
    }

    &__group {
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }

        &:last-child{
            margin-bottom: 0;
        }
    }

    &__group-content {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -12px -24px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0 -8px -16px;
        }

        &>*{
            margin: 0 12px 24px;

            @media only screen and (max-width: $bp-lg) {
                margin: 0 8px 16px;
            }
        }

        .input{
            &--textarea{
                flex-basis: calc(100% - 24px);

                @media only screen and (max-width: $bp-lg) {
                    flex-basis: calc(100% - 16px);
                }
            }

        }

        .input, 
        .select, 
        .button {
            flex: 1 0 calc(50% - 24px);
            max-width: none;

            @media only screen and (max-width: $bp-lg) {
                flex-basis: calc(50% - 16px);
            }

            @media only screen and (max-width: $bp-md) {
                flex-basis: calc(100% - 16px);
            }
        }

        .button{
            @media only screen and (max-width: $bp-lg) {
                height: 44px;
                font-size: 16px;
            }
        }

        .input-img{
            height: 100%;
            width: 136px;
  
            @media only screen and (max-width: $bp-md) {
                width: 148px;
                margin-right: auto;
                margin-left: auto;
            }
        }
    }

    &__group-title {
        color: #000;
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 12px;    
    }

    .schedule-picker{
        &__input{
    		max-width: calc(50% - 12px) !important;

            @media only screen and (max-width: $bp-lg) {
                max-width: calc(50% - 8px) !important;
            }

            @media only screen and (max-width: $bp-md) {
                max-width: none !important;
            }
        }
    }

    &__radio {
        width: calc(100% - 8px);
        display: flex;
        flex-wrap: wrap;
        margin: 0 4px 8px;

        @media only screen and (max-width: $bp-lg) {
            margin: 0;
            width: 100%;
        }

        .radio{
            margin: 0 8px 16px;
        }
    }

    &__buttons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: auto;

        .button{
            flex: 1 0 50%;
            height: 44px;
            font-size: 16px;

            &:last-child{
                margin-bottom: 0;
            }

            @media only screen and (max-width: $bp-md) {
                flex-basis: 100%;
                margin-bottom: 8px;
            }

            &--ghost{
                @media only screen and (max-width: $bp-md) {
                    background-color: $light;
                    color: $dark-light;
                    fill: $dark-light;
                    border: 1px solid #E5E7EB;
            
                    &:hover{
                        background-color: $light-light;
                    }
            
                    &:active{
                        background-color: #E5E7EB;
                        border-color: #D1D5DB;
                    }
                }
            }
        }
    }
}


.modal-info {
    color: $dark;

    &__nav {
        box-sizing: border-box;
        display: none;
        align-items: center;
        margin-bottom: 24px;
        background-color: $light;
        padding: 4px;
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }
    }

    &__nav-button {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1 0 50%;
        padding: 0 5px;
        height: 40px;
        background-color: transparent;
        fill: $light-medium;
        color: $light-medium;
        font-weight: 600;
        font-size: 14px;
        line-height: 100%;
        transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease, opacity 0.35s ease, box-shadow 0.35s ease;
                    
        &:hover{
            background-color: $light-light;
        }

        &:active{
            background-color: #E5E7EB;
        }

        &.is-active{
            background-color: $blue;
            color: #fff;
            fill: #fff;
            
            &:hover{
                background-color: $blue-dark;
            }

            &:active{
                background-color: $blue-light;
            }
        }
    }

    &__body {
        display: flex;
        flex-direction: column;
    }

    &__item {
        display: flex;
        flex-direction: column;
        margin-bottom: 12px;

        &:last-child{
            margin-bottom: 0;
        }

        @media only screen and (max-width: $bp-lg) {
            display: none;

            &.is-active{
                display: block;
                animation: fadeIn 0.5s ease forwards;
            }
        }
    }

    &__group {
        display: flex;
        flex-direction: column;
        margin-bottom: 12px;

        &>*:last-child, 
        &:last-child{
            margin-bottom: 0;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 16px;
        line-height: 24px;
        margin-bottom: 12px;
    }


    &__link {
        display: inline-flex;
        align-items: center;
        color: $gray-blue;
        fill: $orange;
        text-decoration: none;
        transition: color 0.35s ease;
        margin-bottom: 12px;

        .icon{
            margin-right: 8px;
        }

        &:hover{
            color: $blue;
        }
    }

    .list{
        margin-bottom: 12px;

        &:last-child{
            margin-bottom: 0;
        }

        &__item{
            color: inherit;
            margin-bottom: 8px;
        }
    }

    &__button {
        margin-top: 20px;
    }
}